var self;

var webapiaddress = null; //链接地址

import { mapState, mapMutation } from 'vuex';
import http from '@/components/utils/http.js';
export default {
	computed: mapState(['hasLogin']),
	data() {
		return {
			username: '',
			password: '',
			rememberPsw: true,
			array: [  '督导员','用户'],
			jq:0
		};
	},
	methods: {
		typeChange(e) {
			this.index=e.target.value;
			this.jq=this.index;
		},
		submitlogin() {
			if (this.username != '' && this.username != null && (this.password != '' && this.password != null)) {
				let opts = {
					url: 'api/oauth/auth',
					method: 'post'
				};
				http.httpRequest(opts, {
					username: this.username,
					password: this.password
				})
					.then(res => {
						if (res.data.code == 200) {
							let token = res.data.data;
							try {
								uni.setStorageSync('token', token);
							} catch (e) {
								//TODO handle the exception
							}
							// uni.setStorage({
							// 	key:'token',
							// 	data:res.data.data,
							// 	success: function (){},
							// 	fail: function () {
							// 		console.log('缓存token失败')
							// 	}
							// })

							http.httpTokenRequest({
								url: 'api/v1/account/profile',
								method: 'get'
							}).then(res => {
								if (res.data.code == 200) {
									console.log(res.data);
									let user = {
										userName: res.data.data.user_name,
										userId: res.data.data.user_guid,
										userType: res.data.data.user_type,
										DepartmentName: res.data.data.user_departmentName,
										DepartmentGuid: res.data.data.user_departmentGuid,
										RoleName: res.data.data.user_roleName,
										token: token
									};
									this.$store.commit('login', user);
									//登录成功将用户名密码存储到用户本地
									if (this.rememberPsw) {
										//用户勾选“记住密码”
										uni.setStorageSync('username', this.username);
										uni.setStorageSync('password', this.password);
									} else {
										//用户没有勾选“记住密码”
										uni.removeStorageSync('username');
										uni.removeStorageSync('password');
										this.username = '';
										this.password = '';
									}
									uni.reLaunch({
										url: '../home/index'
									});
								} else {
									uni.showModal({
										title: '提示',
										content: res.data.message
									});
								}
							});
						} else {
							uni.showModal({
								title: '提示',
								content: res.data.message
							});
						}
					})
					.catch(res => {
						uni.showModal({
							title: '提示',
							content: '网络服务报错，请检查您的网络'
						});
					});
			} else {
				uni.showModal({
					title: '提示',
					content: '用户名和密码不能为空',
					showCancel: false
				});
			}
		},
		checkboxChange: function(e) {
			console.log(e.detail.value.length);
			if (e.detail.value.length == 1) {
				//获取缓存的账号
				uni.getStorageSync('username', this.username);
				uni.getStorageSync('password', this.password);
			} else {
				uni.removeStorageSync('username');
				uni.removeStorageSync('password');
			}
		}
	},
	//页面初始加载
	mounted() {
		//缓存的账号
		const username = uni.getStorageSync('username');
		//缓存的密码
		const password = uni.getStorageSync('password');
		//有缓存就赋值给文本没有就清空
		if (username && password) {
			this.username = username;
			this.password = password;
		} else {
			this.username = '';
			this.password = '';
		}
	},
	onLaunch: function() {
		uni.getSystemInfo({
			success: function(e) {
				Vue.prototype.statusBar = e.statusBarHeight;









				let custom = wx.getMenuButtonBoundingClientRect();
				Vue.prototype.customBar = custom.bottom + custom.top - e.statusBarHeight;





			}
		});
	}
};
